
// import Section from './components/Phonebook/Section'
import Phonebook from "./components/Phonebook/Phonebook";
import Contacts from './components/Contacts/Contacts';
import Filter from './components/filter/Filter'
import { useState, useEffect, useRef } from 'react';


const defaultContacts =  [
    {id: 'id-1', name: 'Rosie Simpson', number: '459-12-56'},
    {id: 'id-2', name: 'Hermione Kline', number: '443-89-12'},
    {id: 'id-3', name: 'Eden Clements', number: '645-17-79'},
    {id: 'id-4', name: 'Annie Copeland', number: '227-91-26'},
  ]
function App () {

  const isFirstRender = useRef(true);
     
  const [contacts, setcontacts] = useState([]);
  const [filter, setfilter] = useState('');
  
function handleSubmitForm (name, number){

  !filteredContacts(name, number)
  ? setcontacts(contact  =>[ {name: name, number: number},...contact])
   : alert(`${name} is already in contacts`);
      
  }
const filteredContacts = (name, number) => {
    
      return contacts.find(contact => contact.name === name) 
     
    }

const changeFilter = e => {
     setfilter(e.currentTarget.value)
    };
  
  
    
const getVisibleContact = () => {      
      const normalizedFilter = filter.toLowerCase();
       return contacts.filter(contact =>
        contact.name.toLowerCase().includes(normalizedFilter),
      );
    };
 const  deleteContact = name => {
        setcontacts(contacts => contacts.filter(contact => contact.name !== name))
  };

useEffect(() => {
   
 const refsContacts = JSON.parse(localStorage.getItem('contacts'))

 if (refsContacts) {
  setcontacts(refsContacts)
  return
 }
       setcontacts(defaultContacts)
  }, []);

useEffect(() => {
    if (isFirstRender.current) {
      isFirstRender.current = false;
            return     
    }
      window.localStorage.setItem('contacts', JSON.stringify(contacts));
  
     }, [contacts]);

    return(

<div>

<Phonebook submitForm={handleSubmitForm}/>

<Filter onChange={changeFilter} />

  <Contacts onDeleteContact={deleteContact} contact={getVisibleContact()} contacts={contacts} />

</div>
  )}


export default App

